export default function renderMarkup(imgArray = []) {
    const markup = imgArray
        .map(imgEl => {
            const {
                webformatUrl,
                largeImageUrl,
                tags,
                likes,
                views,
                comments,
                downloads,
            } = imgEl;

            return `<div class="photo-card">
  <img src="" alt="" loading="lazy" />
  <div class="info">
    <p class="info-item">
      <b>Likes</b>
    </p>
    <p class="info-item">
      <b>Views</b>
    </p>
    <p class="info-item">
      <b>Comments</b>
    </p>
    <p class="info-item">
      <b>Downloads</b>
    </p>
  </div>
</div>`;
        })
        .join('');
    return markup;
}